@import '../../utils/styles/colors.scss';

//Search-bar

.Container-search-bar-dashboard{
  display: none;
  @media (max-width: 1080px) {
    display: flex;
    justify-content: center;
    width: 80px;
   }
}

.Container-search-bar {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  @media (max-width: 1080px) {
    display: none;
   }
}
.Search-bar {
  width: auto;
  min-width: 500px;
  height: 50px;
  background: $gris;
  display: flex;
  align-items: center;
  border-radius: 60px;
  padding: 10px;
}

.Search-bar-dashboard{
  display: flex;
  justify-content: center;
  width: 380px;
  height: 50px;
  background: $noir;
  border-radius: 60px;
  padding: 10px;
  margin-bottom: 20px;
}

.Search-bar input, .Search-bar-dashboard input {
  background: transparent;
  flex: 1;
  border: 0;
  padding: 9px 20px;
  font-size: 20px;
  color: #cccc;
}
::placeholder {
  color: $blanc;
}
.Search-bar button, .Search-bar-dashboard button {
  border: 0;
  border-radius: 50%;
  margin-left: 10px;
  width: 50px;
  height: 50px;
  cursor: pointer;
  color: $blanc;
  background-color: $rouge;
}

 .Search-bar-dashboard button svg {
  margin: 0px;
}

.Search-bar button:hover, .Search-bar-dashboard button:hover {
  color: $noir;
  background-color: $rouge;
}

