@import '../../utils/styles/colors.scss';

//NavBar
.Logo {
  width: 350px;
}
.Container-navbar {
  display: flex;
  flex-wrap: nowrap;
  background-color: black;
  position: fixed;
  width: 100%;
  box-shadow: 0px 5px 8px -2px rgba(0, 0, 0, 0.51);
  -webkit-box-shadow: 0px 5px 8px -2px rgba(0, 0, 0, 0.51);
  -moz-box-shadow: 0px 5px 8px -2px rgba(0, 0, 0, 0.51);
  @media (max-width: 1080px) {
    top: 0px;
    position: relative;
    z-index: 20;
    justify-content: center;
  }
}

