@import '../../utils/styles/colors.scss';

footer{
  display: flex;
  position: fixed;
  width: 30%;
  top: 90%;
  left: 10%;
  height: 500px;
  @media (max-width: 1080px) {
    flex-direction: column;
    justify-content: center;
    position: relative;
    width: auto;
    height: 100px;
    flex-wrap: wrap;
    left: 0;
   }
}

footer div{
  display: flex;
  flex-direction: column;
  @media (max-width: 1080px) {
    justify-content: center;
    align-items: center;
    width: auto;
   }
}

footer div p{
  font-size: 12px;
 color: $blanc;
 margin-left: 20px;
 @media (max-width: 1080px) {
  width: 100%;
  height: 30px;
 }
}


footer a{
  color: $rouge;
  margin-left: 20px;
  text-decoration: none;
}



footer a:hover{
  color: $rouge-hover;
}

.justelire{
  display: flex;
  flex-direction: row;
  @media (max-width: 1080px) {
    width: 500px;
    height: auto;
   }
}