@import '../../utils/styles/colors.scss';

//Card Article
article{
  display: flex;
  justify-content: center;
  align-items:center;
  position: relative;
  flex-direction: column;
  width: 33%;
  height: 700px;
  background-color: $gris-claire;
  border-radius: 10px;
  margin-bottom: 20px;
  margin-left: 350px;
  z-index: -1;
  @media (max-width: 1080px) {
    width: 95%;
    margin-left: 0px;
   }
}

h1{
  text-align: center;
  width: 250px;
  color: $blanc;
  font-size: 25px;
  margin: 0px;
}


article img{
  width: 100%;
  height: 400px;
 
}

.rating{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 500px;
  height: 100px;
}

.fa-star{
  color: $rouge;
  font-size: 18px;
}

.Container-social{
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 90%;
  height: 10px;
  border-top: 1px solid $blanc;
  margin-top: 20px;
}

.Container-social .Items{
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 30px;
}

.Container-social .Items svg{
  width: 30px;
}

.Container-social .Items p{
  color: $blanc;
  cursor: pointer;
}

.Container-social .Items p:hover{
  color: $rouge;
}

.fa-share, .fa-comment, .fa-thumbs-up{
  color: $rouge;
}

.Like {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 93%;
  height: 0px;
  margin-top: 20px;
  margin-bottom: 0px;
  margin-left: 20px;
}

.Like p{
  color: $blanc;
  margin-left: 5px;
  font-size: 18px;
  margin-top: 21px;
}

.fa-heart{
  font-size: 18px;
  color: $rouge;
}